// exchange volume
export const CHANGE_EXCHANGE_VOLUMES_PAIR = "CHANGE_EXCHANGE_VOLUMES_PAIR";
export const UPDATE_EXCHANGE_VOLUMES_DATA = "UPDATE_EXCHANGE_VOLUMES_DATA";
export const FETCH_EXCHANGE_VOLUMES_DATA = "FETCH_EXCHANGE_VOLUMES_DATA";
export const EXCHANGE_VOLUMES_FAILED = "EXCHANGE_VOLUMES_FAILED";

export const changeExchangeVolumesPair = syms => ({
  type: CHANGE_EXCHANGE_VOLUMES_PAIR,
  payload: syms
});

export const updateExchangeVolumesData = data => ({
  type: UPDATE_EXCHANGE_VOLUMES_DATA,
  payload: data
});

export const fetchExchangeVolumesData = data => ({
  type: FETCH_EXCHANGE_VOLUMES_DATA
});

export const exchangeVolumesFailed = message => ({
  type: EXCHANGE_VOLUMES_FAILED,
  payload: message
});



