import "regenerator-runtime/runtime";
import { all } from "redux-saga/effects";

import exchangeVolumesSaga from "src/Visualizations/ExchangeVolumes/Sagas";
import opensSaga from "src/Visualizations/Opens/Sagas";

export default function* rootSaga() {
  yield all([exchangeVolumesSaga(), opensSaga()]);
}



