import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { fetchOpensData } from "src/Visualizations/Opens/Actions";
import { fetchExchangeVolumesData } from "src/Visualizations/ExchangeVolumes/Actions";

import { Switch, Route } from "react-router";
import ExchangeVolumesViz from "src/Visualizations/ExchangeVolumes/ExchangeVolumesViz";
import OpensViz from "src/Visualizations/Opens/OpensViz";
import VizList from "src/VizList";
import VizTest1 from "src/Visualizations/VizTest1/VizTest1";
import VizTest2 from "src/Visualizations/VizTest2/VizTest2";

const PageContent = props => {
  const vizList = [
    {
      name: "exchange volumes",
      description:
        "shows volumes for a currency pair across different crpto exchanges",
      path: "/viz/exchangeVolumes",
      component: ExchangeVolumesViz,
      fetch: () => {
        props.fetchExchangeVolumesData();
      }
    },
    {
      name: "opens",
      description:
        "charts the opening prices for a pair of crpytocurrencies across various markets",
      path: "/viz/opens",
      component: OpensViz,
      fetch: () => {
        props.fetchOpensData();
      }
    },
    {
      name: "VizTest1",
      description: "just a test",
      path: "/viz/vizTest1",
      component: VizTest1,
      fetch: () => {
        console.log("fetched");
      }
    },
    {
      name: "VizTest2",
      description: "just a test",
      path: "/viz/vizTest2",
      component: VizTest2,
      fetch: () => {
        console.log("fetched");
      }
    }
  ];
  return (
    <Switch>
      <Route exact path="/" render={() => <VizList vizList={vizList} />} />
      {vizList.map((viz, i) => {
        return (
          <Route
            key={i}
            path={viz.path}
            render={() => {
              console.log("call fetch");
              viz.fetch();
              return <viz.component />;
            }}
          />
        );
      })}
    </Switch>
  );
};

export default connect(
  null,
  d => bindActionCreators({ fetchOpensData, fetchExchangeVolumesData }, d)
)(PageContent);



