import * as Actions from "./Actions";
import { fetchOpensData } from "src/Api";
import { put, takeEvery, call, select } from "redux-saga/effects";

function* opensSaga(action) {
  const state = yield select();
  const r = yield call(fetchOpensData, state.opens.pair);
  yield put(Actions.updateOpensData(r));
}

function* fetchData() {
  yield put(Actions.fetchOpensData());
}

export default function* watchAll() {
  yield takeEvery(Actions.FETCH_OPENS_DATA, opensSaga);
  yield takeEvery(Actions.CHANGE_OPENS_PAIR, fetchData);
}



