// @flow

import * as React from "react";
import type { AxisDefinition, Margins } from "./FlowTypes";
// import Axes from "./Axes";
import Axis from "./Axis";
// import ChartWrapper from "./ChartWrapper";
type Props = {
  axisDefinitions: Array<AxisDefinition>,
  chart: mixed,
  margins: Margins,
  bgColor: string,
  leftAxis: Axis,
  bottomAxis: Axis,
  graph: () => React.Node
};

class Chart extends React.Component<Props> {
  static defaultProps = {
    graph: () => null
  };
  static Axis = Axis;
  render() {
    const { graph, margins, bgColor, leftAxis, bottomAxis } = this.props;
    let xScaling = (100 - (margins.left + margins.right)) / 100;
    let yScaling = (100 - (margins.top + margins.bottom)) / 100;

    return (
      <svg
        style={{ width: "100%", height: "100%", backgroundColor: bgColor }}
        preserveAspectRatio="none"
        viewBox={"0 0 100 100"}
      >
        {/* top */}
        <line
          x1={margins.left}
          y1={margins.top}
          x2={100 - margins.right}
          y2={margins.top}
          stroke="purple"
        />
        {/* bottom */}
        <line
          x1={margins.left}
          y1={100 - margins.bottom}
          x2={100 - margins.right}
          y2={100 - margins.bottom}
          stroke="purple"
        />
        {/* left */}
        <line
          x1={margins.left}
          y1={margins.top}
          x2={margins.left}
          y2={100 - margins.bottom}
          stroke="purple"
        />
        {/* right */}
        <line
          x1={100 - margins.right}
          y1={margins.top}
          x2={100 - margins.right}
          y2={100 - margins.bottom}
          stroke="purple"
        />
        {/* left axis */}
        {React.cloneElement(leftAxis, {
          orientation: "bottom",
          margins: margins
        })}

        {/* bottom axis */}
        {/* {React.cloneElement(bottomAxis, {
          orientation: "bottom",
          margins: margins,
        })} */}
        <g
          transform={`translate(${margins.left} ${
            margins.top
          }) scale(${xScaling} ${yScaling})`}
        >
          {graph()}
        </g>
      </svg>
    );
  }
}

export default Chart;



