import React from "react";
import { select, selectAll } from "d3-selection";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import Dimensions from "react-dimensions";

class VizTest1 extends React.Component {
  componentDidMount() {
    this.configureD3();
  }

  configureD3 = () => {
    let height = this.props.containerHeight;

    let dataSet = Array(20)
      .fill()
      .map(f => Math.random() * height);
    console.log("dataSet");
    console.log(dataSet);
    let barWidth = this.props.containerWidth / dataSet.length;

    let svg = select(this.refs.node)
      .append("svg")
      .attr("width", this.props.containerWidth)
      .attr("height", this.props.containerHeight);

    svg
      .selectAll("rect")
      .data(dataSet)
      .enter()
      .append("rect")
      .attr("x", (foo, i) => `${i * barWidth}`)
      .attr("y", (d, i) => height - d)
      .attr("height", d => d)
      .attr("width", barWidth - 1)
      .style("fill", foo => `rgb(${foo},${foo},${foo})`);
    // .text(foo => `${foo}`);

    svg
      .selectAll("text")
      .data(dataSet)
      .enter()
      .append("text")
      .text(d => Math.round(d))
      .attr("x", (foo, i) => `${i * barWidth + barWidth / 2}`)
      .attr("y", d => height - d)
      .attr("font-family", "sans-serif")
      .attr("font-size", "11px")
      .attr("fill", "white")
      .attr("text-anchor", "middle");
  };

  render() {
    return <div ref="node" />;
  }
}

const e = Dimensions()(VizTest1);
export default e;



