import "regenerator-runtime/runtime";
import { delay } from "redux-saga";
import * as Actions from "./Actions";
import { put, takeEvery, call, select } from "redux-saga/effects";
import { fetchExchangeVolumesData } from "src/Api";

function* saga(action) {
  try {
    const state = yield select();

    const pair = state.exchangeVolumes.pair;

    if (pair.fsym == pair.tsym) {
      throw "";
    }

    const r = yield call(fetchExchangeVolumesData, state.exchangeVolumes.pair);
    yield put(Actions.updateExchangeVolumesData(r));
  } catch (error) {
    yield put(Actions.exchangeVolumesFailed("fsym same as tsym"));
  }
}

export default function* watchAll() {
  yield takeEvery(Actions.CHANGE_EXCHANGE_VOLUMES_PAIR, saga);
  yield takeEvery(Actions.FETCH_EXCHANGE_VOLUMES_DATA, saga);
}



