// @flow
import React from "react";
import type { Orientation } from "./FlowTypes";

type Props = {
  text: string,
  opacity: number,
  size: number,
  orientation: Orientation
};

let Tick = (props: Props) => {
  const { orientation, distance, opacity, text, transform } = props;

  return (
    <g transform={transform}>
      <rect x="0" y="0" width="100" height="100" fill="grey" />
      <line x1="50" y1="0" x2="50" y2="100" stroke="white" />
      <rect x="99" y="0" width="1" height="100" stroke="black" />
      <rect x="0" y="1" width="1" height="100" stroke="black" />
      {/* <rect
        x="0"
        y="10"
        width="100"
        height="80"
        fill="blue"
        opacity={opacity}
      />
      <line {...getLineAttrs(30)[orientation]} stroke="white" opacity={opacity} />
      <text {...getTextAttrs(30)[orientation]} style={{ fontSize: "0.3em".tick, fill: "white" }}>
        {text}
      </text> */}
    </g>
  );
};

Tick.defaultProps = { opacity: 0.6 };

let getTranslateAttrs = distance => ({
  left: {
    x: 0,
    y: distance
  },
  right: {
    x: 0,
    y: distance
  },
  top: {
    x: distance,
    y: 0
  },
  bottom: {
    x: distance,
    y: 0
  }
});

let getScaleAttrs = distance => ({
  left: {
    width: 0,
    height: distance
  },
  right: {
    width: 0,
    height: distance
  },
  top: {
    width: distance,
    height: 0
  },
  bottom: {
    width: distance,
    height: 0
  }
});

let getLineAttrs = tickLength => ({
  top: {
    x1: 100 - tickLength,
    x2: 100,
    y1: 50,
    y2: 50
  },
  left: {
    x1: 100 - tickLength,
    x2: 100,
    y1: 50,
    y2: 50
  },
  right: {
    x1: 100 - tickLength,
    x2: 100,
    y1: 50,
    y2: 50
  },
  bottom: {
    x1: 100 - tickLength,
    x2: 100,
    y1: 50,
    y2: 50
  }
});

let getTextAttrs = tickLength => ({
  left: { x: 0, y: 50 },
  top: { x: 100 - tickLength, y: 100 },
  right: { x: 100 - tickLength, y: 100 },
  bottom: { x: 100 - tickLength, y: 100 }
});

export default Tick;



