import Axios from "axios";

export const fetchOpensData = ({ fsym, tsym }) => {
  return new Promise(resolve => {
    Axios({
      url: "/opens",
      params: {
        fsym,
        tsym
      }
    }).then(res => {
      const { data } = res;
      resolve(data);
    });
  });
};

export const fetchExchangeVolumesData = ({ fsym, tsym }) => {
  return new Promise(resolve => {
    Axios({
      url: "/exchangeVolumes",
      params: {
        fsym,
        tsym
      }
    }).then(res => {
      const { data } = res;
      resolve(data);
    });
  });
};

