import React from "react";
import PieChart from "src/Components/PieChart/PieChart";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { changeExchangeVolumesPair } from "./Actions";
import SymbolsSelector from "src/Components/SymbolsSelector";
import { Centered } from "src/Style";

const ExchangeVolumesViz = props => (
  <div
    style={{
      display: "grid",
      gridTemplateRows: "11fr 1fr",
      height: "100%"
    }}
  >
    {props.error ? (
      <div>error</div>
    ) : (
      <Centered>
        <PieChart
          data={props.data.map(d => ({
            label: d.exchange,
            value: d.volume
          }))}
        />
      </Centered>
    )}

    <SymbolsSelector
      initialPair={props.pair}
      pairSelected={pair => props.changeExchangeVolumesPair(pair)}
      symbols={props.symbols}
    />
  </div>
);

const mProps = (state, ownProps) => {
  const { exchangeVolumes } = state;

  return {
    data: exchangeVolumes.data,
    pair: exchangeVolumes.pair,
    symbols: exchangeVolumes.symbols,
    error: exchangeVolumes.error
  };
};

const mDisp = d => bindActionCreators({ changeExchangeVolumesPair }, d);

export default connect(
  mProps,
  mDisp
)(ExchangeVolumesViz);



