import { combineReducers } from "redux";
import * as Actions from "./Actions";

const initialExchangeVolumeData = [
  { label: "a", value: 1 },
  { label: "b", value: 3 }
];

const data = (state = initialExchangeVolumeData, action) => {
  switch (action.type) {
    case Actions.UPDATE_EXCHANGE_VOLUMES_DATA:
      return action.payload;
    default:
      return state;
  }

  return state;
};

const pair = (state = { fsym: "BTC", tsym: "USD" }, action) => {
  switch (action.type) {
    case Actions.CHANGE_EXCHANGE_VOLUMES_PAIR:
      return action.payload;
    default:
      state;
  }

  return state;
};

const symbols = (state = ["BTC", "ETH", "USD"], action) => {
  return state;
};

const error = (state = null, action) => {
  switch (action.type) {
    case Actions.EXCHANGE_VOLUMES_FAILED:
      return action.payload;
    default:
      return null;
  }
};

export default combineReducers({
  pair,
  data,
  symbols,
  error
});



