import { combineReducers } from "redux";
import * as Actions from "./Actions";

const pair = (state = { fsym: "BTC", tsym: "USD" }, action) => {
  switch (action.type) {
    case Actions.CHANGE_OPENS_PAIR:
      return action.payload;
    default:
      return state;
  }

  return state;
};

const data = (state = [], action) => {
  switch (action.type) {
    case Actions.UPDATE_OPENS_DATA:
      return action.payload;
    default:
      return state;
  }

  return state;
};

const symbols = (state = ["BTC", "ETH", "USD", "EUR"], action) => {
  return state;
};

export default combineReducers({
  pair,
  data,
  symbols
});



