import React from "react";
import { Link } from "react-router-dom";

import Styled from "styled-components";
import { Centered } from "src/Style";

const VizItemsContainer = Styled.section`
  padding: 5%;
  height: 100%;
  display: grid;
  grid-gap: 5%;
  grid-template-columns: 1fr 1fr 1fr;
  // grid-template-rows: 1fr 1fr 1fr;
	align-items: center;
	justify-content: flex-start;
	background-color: clear;
`;

const VizItem = Styled.div`
  background-color: ${props => props.theme.secondary.bgColor};
  display: grid;
  height: 100%;
  padding: 5%;
  grid-gap: 10%;
  grid-template-rows: 1fr 2fr;
  border-width: 1px;
  border-color: #ffffff1c;
  border-style: solid;
`;

const TitleText = Styled.div`
text-decoration: none;
text-align: center;
color: ${props => props.theme.primary.textColor}
font-size: ${props => props.theme.primary.fontSize}
`;
const DescriptionText = Styled.div`
text-decoration: none;
text-align: center;
color: ${props => props.theme.secondary.textColor}
font-size: ${props => props.theme.secondary.fontSize}
`;

const VizItemComponent = ({ name, description, path }) => {
  return (
    <Link style={{ height: "100%", textDecoration: "none" }} to={path}>
      <VizItem>
        <Centered>
          <TitleText>{name}</TitleText>
        </Centered>
        <Centered>
          <DescriptionText>{description}</DescriptionText>
        </Centered>
      </VizItem>
    </Link>
  );
};

const VizList = props => {
  return (
    <VizItemsContainer>
      {props.vizList.map((viz, i) => {
        return (
          <VizItemComponent
            key={i}
            name={viz.name}
            path={viz.path}
            description={viz.description}
          />
        );
      })}
    </VizItemsContainer>
  );
};

export default VizList;



