import styled, { ThemeProvider, injectGlobal } from "styled-components";

import React from "react";

import { withRouter } from "react-router-dom";
import { Context as ThemeContext } from "src/ThemeProvider";

injectGlobal`
  body {
    height: 100%;
    width: 100%;
    margin: 0;
    background-color: white;
  }

  * {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
`;

const Centered = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
`;

const Widget = styled.div``;

export { Centered };

const StyledBackButton = styled.button`
width: 80%;
height: 80%
background-color: ${props => props.theme.primary.bgColor}
color: ${props => props.theme.primary.fgColor}
`;

const Back = ({ history }) => (
  <StyledBackButton onClick={history.goBack}>Back</StyledBackButton>
);

var BB = withRouter(Back);

const Window = styled.div`
background-color: ${props => props.theme.primary.bgColor}
padding: 5%;
height: 100vh;
  width: 100vw;
`;

const PageWrapper = styled.div`
  height: 70%
  width: 70%;
  overflow: hidden;
  grid-gap: 3%;
  display: grid;
  justify-items: normal;
  background-color: clear;
  grid-template-rows: 1fr 9fr;
`;

const PageTitle = styled.div`
  font-size: ${props => props.theme.primary.fontSize};
  color: ${props => props.theme.primary.textColor};
  background-color: clear;
`;

const TopBarWrapper = styled.div`
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr 7fr;
  background-color: ${props => props.theme.tersiary.bgColor};
`;

const ContentWrapper = styled.section`
  background-color: ${props => props.theme.tersiary.bgColor};
  height: 100%;
`;

const TopBar = props => (
  <TopBarWrapper>
    {props.showBack && (
      <Centered>
        <BB />
      </Centered>
    )}
    <Centered>
      <PageTitle>{props.title}</PageTitle>
    </Centered>
  </TopBarWrapper>
);

const Page = props => {
  return (
    <ThemeContext.Consumer>
      {theme => {
        return (
          <ThemeProvider theme={theme}>
            <Window>
              <PageWrapper>
                <TopBar title={props.title} showBack={!props.isRoot} />
                <ContentWrapper>{props.pageContent}</ContentWrapper>
              </PageWrapper>
            </Window>
          </ThemeProvider>
        );
      }}
    </ThemeContext.Consumer>
  );
};

export default Page;



