import React, { Component } from "react";
import { curveLinear as d3CurveLinear, line as d3Line } from "d3-shape";
import { Spring } from "react-spring";

class LinePlot extends Component {
  render() {
    const { xScale, yScale, selectX, selectY, data, strokeColor } = this.props;

    const d = d3Line()
      .x(d => xScale(selectX(d)))
      .y(d => yScale(selectY(d)))
      .curve(d3CurveLinear)(data);

    return (
      <Spring
        to={{
          path: d
        }}
      >
        {m => <path style={{ stroke: strokeColor, fill: "none" }} d={m.path} />}
      </Spring>
    );
  }
}

export default LinePlot;



