import React from "react";
import { max } from "d3-array";
import { scaleLinear } from "d3-scale";
import Dimensions from "react-dimensions";
import ScatterPlot from "src/Components/Chart/ScatterPlot";

import Chart from "src/Components/Chart/Chart";

class VizTest2 extends React.Component {
  counter = 0;

  getNewState = () => {
    this.counter++;

    if (this.counter >= 3) {
      this.counter = 0;
    }

    let datas = [
      [[1, 2], [13, 4], [1, 11]],
      [[2, 5], [1, 4], [1, 4]],
      [[2, 9], [3, 4], [60, 50]]
    ];

    let data = datas[this.counter];
    // let data = Array(50)
    //   .fill()
    //   .map(() => [Math.random() * 25, Math.random() * 30]);

    // if (Math.random() > 0.7) {
    //   data.push([60, 80]);
    // }

    // if (Math.random() < 0.2) {
    //   data.push([300, 0]);
    // }

    // if (Math.random() > 0.8) {
    //   data.push([0, 900]);
    // }

    let state = {
      data: data,

      xScale: scaleLinear()
        .domain([0, max(data, d => d[0])])
        .range([0, 100]),

      yScale: scaleLinear()
        .domain([0, max(data, d => d[1])])
        .range([100, 0]),
      rScale: scaleLinear()
        .domain([0, max(data, d => d[1])])
        .range([0.2, 8]),

      cScale: scaleLinear()
        .domain([0, max(data, d => d[1])])
        .range([0, 255])
    };

    return state;
  };
  updateData = () => {
    let state = this.getNewState();
    this.setState(state);
  };

  state = this.getNewState();

  componentDidUpdate() {}

  render() {
    const { cScale, rScale, xScale, yScale, data } = this.state;

    return (
      <div
        style={{
          width: "100%",
          height: "100%",
          display: "grid",
          gridTemplateRows: "4fr 1fr"
        }}
      >
        <Chart
          margins={{ top: 5, left: 5, right: 5, bottom: 5 }}
          axes={[
            { domain: xScale.domain(), ticks: 5, orientation: "left" },
            { domain: yScale.domain(), ticks: 12, orientation: "bottom" }
          ]}
          chart={() => {
            return (
              <ScatterPlot {...{ cScale, rScale, xScale, yScale, data }} />
            );
          }}
        />
        <button onClick={() => this.updateData()}>update</button>
      </div>
    );
  }
}

const e = Dimensions()(VizTest2);
export default e;



