// opens
export const CHANGE_OPENS_PAIR = "CHANGE_OPENS_PAIR";
export const UPDATE_OPENS_DATA = "UPDATE_OPENS_DATA";
export const FETCH_OPENS_DATA = "FETCH_OPENS_DATA";

export const changeOpensPair = syms => ({
  type: CHANGE_OPENS_PAIR,
  payload: syms
});

export const updateOpensData = data => ({
  type: UPDATE_OPENS_DATA,
  payload: data
});

export const fetchOpensData = data => ({
  type: FETCH_OPENS_DATA
});



